const commodityList = r => require.ensure([], () => r(require('@/pages/commodityList/commodityList.vue')), 'commodityList')
const CommodityListRouter = [{
    path: '/commodityList',
    name: 'commodityList',
    component: commodityList
}]
export { CommodityListRouter }


