let source = {};
const request = {

    /**
     * 进度条的网络请求
     * @param {*} that 当前对象
     * @param {*} url 网络请求的url
     * @param {*} method 请求方式(post/get)
     * @param {*} params 请求参数
     * @param {*} message 请求提示信息
     */
    loading(that, url, method, params, message) {
        source = that.$axios.CancelToken.source();
        // 判断是否显示请求提示信息
        if (message.length > 0) {
            that.$vux.loading.show({
                text: message
            })
        }
        return new Promise((resolve, reject) => {
            that.$axios.request({
                url: url,
                method: method,
                params: method=='get'?params:{},
                data:method=='post'?params:{},
                timeout: 10000, // 指定请求超时的毫秒数(0 表示无超时时间)，如果请求话费了超过 `timeout` 的时间，请求将被中断
                headers: { 'Content-Type': 'application/json', 'Authorization': 'Bearer undefined1566121344000d41d8cd98f00b204e9800998ecf8427e' },
                cancelToken: source.token,             // 指定用于取消请求的cancel
                onUploadProgress: function (progressEvent) {

                },// 允许为上传处理进度事件
                onDownloadProgress: function (progressEvent) {

                }, // 允许为下载处理进度事件
            }).then((response) => {
                that.$vux.loading.hide();
                console.log(response);
                resolve(response);
            }, (error) => {
                that.$vux.loading.hide();
                console.log(error);
                reject(error);
            })
        });
    },
    /**
     * 网络请求
     * @param {*} that 当前对象
     * @param {*} url 网络请求的url
     * @param {*} method 请求方式(post/get)
     * @param {*} params 请求参数
     */
    notLoading(that, url, method, params) {
        return this.requestLoading(that, url, method, params, "");
    },
    /**
     * 取消网络请求
     */
    cancelRequest() {
        source.cancel();
    }
}
export { request }



