const commoditydec = r => require.ensure([], () => r(require('@/pages/commoditydec/commoditydec.vue')), 'commoditydec')
const CommoditydecRouter = [{
    path: '/commoditydec',
    name: 'commoditydec',
    component: commoditydec
}]
export { CommoditydecRouter }


